<template>
	<div class="container">
  <div class="row" id="translateForm">
  	<div class="col-md-6 col-md-offset-3">
	    <form id="transForm" class="well form-inline" v-on:submit="formSubmit">
	    	<input class="form-control" type="text" placeholder="输入需要翻译的内容" v-model="textToTranslate"/>
	    	<select class="form-control" v-model="language">
	    		<option value="en">English</option>
	    		<option value="ru">Russian</option>
	    		<option value="ko">Korean</option>
	    		<option value="ja">Japanese</option>
	    	</select>
	    	<input class="btn btn-primary" type="submit" value="翻译" />
	    </form>
    </div>
  </div>
  </div>
</template>

<script>
export default {
  name: 'translateForm',
  data () {
    return {
      textToTranslate:"",
      language:""
    }
  },
  methods:{
  	formSubmit:function(e){
//		alert(this.textToTranslate);
      this.$emit("formSubmit",this.textToTranslate,this.language);
  		e.preventDefault();
  	}
  },
  created(){
  		this.language = "en";
  	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#transForm{ border-radius: 10px; border: 1px solid #ccc; text-align: center; }
</style>



